ks-page-account-order {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 1 auto;
}

ks-page-account-order .container {
    max-width: 100%;
    width: 1000px;
    box-sizing: border-box;
    padding: 30px;
    margin: 30px 0px;
    background-color: #fff;
    border-radius: 10px;
}

ks-page-account-order .columns {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
}

ks-page-account-order .columns > * {
    width: 100%;
}

ks-page-account-order .container > .status {
    text-align: center;
    padding: 3px 15px;
    margin: 5px auto 20px auto;
    background-color: black;
    color: white;
    border-radius: 10px;
    width: fit-content;
}

ks-page-account-order ks-cart-product-heading {
    margin-left: 10px;
}

ks-page-account-order .details .entry {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    margin: 10px 0px;
    padding: 10px 20px;

    border: 1px dashed #ccc;
    border-radius: 10px;
}

ks-page-account-order .details .entry >*:last-child {
    font-weight: 700;
}

ks-page-account-order .address {
    padding: 15px 20px;
    border: 1px dashed #ccc;
    border-radius: 10px;
    margin: 10px 0px;
    box-sizing: border-box;
}

ks-page-account-order .address p {
    margin: 0px;
}

ks-page-account-order .container > h2 {
    text-align: center;
}

ks-page-account-order .address h2 {
    margin-bottom: 7px;
}

ks-page-account-order .summary {
    padding: 0px 20px;
    margin: 15px 0px 30px 0px;
}

ks-page-account-order .slot {
    padding: 0px 10px;
    margin: 20px 0px 40px 0px;
    text-align: center;
}

ks-page-account-order .history .entry {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
    grid-template-areas: 
        "status date"
        "comment comment";

    justify-content: space-between;
    padding: 10px 20px;
    border: 1px dashed #ccc;
    border-radius: 10px;
    margin: 10px 0px;
}

ks-page-account-order .history .status {
    grid-area: status;
    font-weight: 700;
}

ks-page-account-order .history .date {
    grid-area: date;
}

ks-page-account-order .history .comment {
    grid-area: comment;
}

ks-page-account-order .buttons {
    display: flex;
    gap: 10px;
    margin-top: 30px;
}

ks-page-account-order .buttons > * {
    width: 100%;
}

ks-page-account-order .container > h1 {
    text-align: center;
    margin: 10px 0px 5px 0px;
}

@media (max-width: 1060px) {
    ks-page-account-order {
        background-color: #fff;
    }

    ks-page-account-order .container {
        padding: 20px;
        margin: 0px;
    }
}

@media (max-width: 640px) {
    ks-page-account-order ks-cart-product-heading {
        margin-left: 0px;
    }

    ks-page-account-order ks-cart-product-heading .names {
        text-align: center !important;
    }

    ks-page-account-order {
        font-size: 14px;
    }

    ks-page-account-order .address h2 {
        font-size: 18px;
    }

    ks-page-account-order .columns {
        flex-direction: column;
        gap: 20px;
        margin-bottom: 50px;
    }

    ks-page-account-order .address {
        margin: 0px;
    }

    ks-page-account-order .products {
        margin-top: 50px;
    }

    ks-page-account-order .summary {
        padding: 0px 5px;
        margin: 20px 0px 50px 0px;
    }
}