import { Component, Host, Prop, State, h } from '@stencil/core';

@Component({
    tag: 'ks-page-account-order',
    styleUrl: 'order.css'
})
export class PageAccountOrder {
    @Prop() accountLink: string;
    @Prop() returnLink: string;

    @Prop() orderJson: string;
    @State() order?: AccountOrderData;

    componentWillLoad() {
        this.order = this.orderJson ? JSON.parse(this.orderJson) : undefined;
    }

	render() {
        if(!this.order) return;

		return <Host>
            <section class="container">
                <h1>{this.order.heading}</h1>
                <div class="status">{this.order.status}</div>

                <div class="products">
                    <ks-cart-product-heading 
                        productTableNames='Produkty'
                        productTableCount='Ilość'
                        productTablePrices='Ceny'
                    />

                    {this.order.products.map(product => <ks-cart-product small
                        name={product.name}
                        link={product.link}
                        shipping-time={product.shipping}
                        img={product.img}
                        price={product.price}
                        amount={product.amount}
                    />)}
                </div>

                <div class="summary">
                    {this.order.summary.map(entry => <ks-cart-summary
                        text={entry.name}
                        price={entry.value}
                        large={entry.large}
                    />)}
                </div>

                <div class="slot">
                    <slot/>
                </div>

                <h2>Szczegóły zamówienia</h2>

                <div class="details">
                    <div class="entry">
                        <div>Data złożenia:</div>
                        <div>{this.order.date}</div>
                    </div>

                    <div class="entry">
                        <div>Metoda płatności:</div>
                        <div>{this.order.payment}</div>
                    </div>

                    <div class="entry">
                        <div>Metoda wysyłki:</div>
                        <div>{this.order.shipping}</div>
                    </div>
                </div>

                <div class="columns">
                    <div class="address">
                        <h2>Adres wysyłki</h2>
                        {this.order.shippingName && <p>{this.order.shippingName}</p>}
                        {this.order.shippingCompany && <p>{this.order.shippingCompany}</p>}
                        {this.order.shippingNIP && <p>{this.order.shippingNIP}</p>}
                        <p>{this.order.shippingStreet}</p>
                        <p>{this.order.shippingCity} {this.order.shippingPostcode}</p>
                        <p>{this.order.shippingCountry}</p>
                    </div>
                    <div class="address">
                        <h2>Adres płatności</h2>
                        {this.order.billingName && <p>{this.order.billingName}</p>}
                        {this.order.billingCompany && <p>{this.order.billingCompany}</p>}
                        {this.order.billingNIP && <p>{this.order.billingNIP}</p>}
                        <p>{this.order.billingStreet}</p>
                        <p>{this.order.billingCity} {this.order.billingPostcode}</p>
                        <p>{this.order.billingCountry}</p>
                    </div>
                </div>

                <h2>Historia zamówienia</h2>
                <div class="history">
                    {this.order.history.map(entry => <div class="entry">
                        <div class="status">{entry.status}</div>
                        <div class="date">{entry.date}</div>
                        <div class="comment">{entry.comment}</div>
                    </div>)}
                </div>

                <div class="buttons">
                    <ks-button rounded name="Cofnij" link={this.returnLink} />
                    <ks-button rounded name="Panel klienta" link={this.accountLink} />
                </div>
            </section>
        </Host>;
	}
}

export interface AccountOrderData {
    heading: string;
    status: string;
    date: string;
    payment: string;
    shipping: string;

    shippingName: string;
    shippingCompany: string;
    shippingNIP: string;
    shippingStreet: string;
    shippingCity: string;
    shippingPostcode: string;
    shippingCountry: string;

    billingName: string;
    billingCompany: string;
    billingNIP: string;
    billingStreet: string;
    billingCity: string;
    billingPostcode: string;
    billingCountry: string;

    products: {
        name: string;
        link: string;
        shipping: string;
        img: string
        price: number
        amount: number
    }[];
    summary: {
        name: string;
        value: number;
        large: boolean;
    }[],
    history: {
        status: string;
        date: string;
        comment: string;
    }[]
}